'use client';

import { useState, useRef, useEffect } from 'react';
import { X, Send, Loader } from 'lucide-react';

interface Message {
  id: string;
  text: string;
  sender: 'user' | 'bot';
  timestamp: Date;
}

export default function ChatBot() {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      text: '¡Hola! Soy tu asistente de IA. ¿En qué puedo ayudarte?',
      sender: 'bot',
      timestamp: new Date(),
    },
  ]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!inputValue.trim()) return;

    // Add user message
    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputValue,
      sender: 'user',
      timestamp: new Date(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);

    try {
      // Call Gemini API
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: inputValue,
        }),
      });

      const data = await response.json();

      const botMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: data.response || 'Lo siento, no pude procesar tu solicitud.',
        sender: 'bot',
        timestamp: new Date(),
      };

      setMessages((prev) => [...prev, botMessage]);
    } catch (error) {
      console.error('Error:', error);
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: 'Error al conectar con el servicio. Por favor, intenta de nuevo.',
        sender: 'bot',
        timestamp: new Date(),
      };
      setMessages((prev) => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      {/* Chat Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className={`fixed bottom-6 right-6 w-16 h-16 rounded-full bg-gradient-to-r from-teal-500 to-teal-600 shadow-lg shadow-teal-500/50 hover:shadow-teal-500/70 flex items-center justify-center cursor-pointer transition-all duration-300 hover:-translate-y-1 z-40 ${
          isOpen ? 'scale-95' : 'scale-100'
        }`}
      >
        <svg
          width="40"
          height="40"
          viewBox="0 0 40 40"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          className="text-white"
        >
          {/* Robot Head */}
          <rect x="6" y="4" width="28" height="20" rx="2" fill="white" />
          
          {/* Eyes */}
          <rect x="10" y="8" width="6" height="6" rx="1" fill="#0d9488" />
          <rect x="24" y="8" width="6" height="6" rx="1" fill="#0d9488" />
          
          {/* Mouth */}
          <path d="M 12 16 Q 20 19 28 16" stroke="#0d9488" strokeWidth="1.5" fill="none" strokeLinecap="round" />
          
          {/* Antenna Left */}
          <line x1="10" y1="4" x2="10" y2="0" stroke="white" strokeWidth="2" strokeLinecap="round" />
          <circle cx="10" cy="0" r="1.5" fill="white" />
          
          {/* Antenna Right */}
          <line x1="30" y1="4" x2="30" y2="0" stroke="white" strokeWidth="2" strokeLinecap="round" />
          <circle cx="30" cy="0" r="1.5" fill="white" />
          
          {/* Body */}
          <rect x="12" y="24" width="16" height="12" rx="2" fill="white" />
          
          {/* Buttons */}
          <circle cx="16" cy="28" r="1.5" fill="#0d9488" />
          <circle cx="20" cy="28" r="1.5" fill="#0d9488" />
          <circle cx="24" cy="28" r="1.5" fill="#0d9488" />
          
          {/* Arms */}
          <rect x="2" y="28" width="10" height="3" rx="1.5" fill="white" />
          <rect x="28" y="28" width="10" height="3" rx="1.5" fill="white" />
        </svg>
      </button>

      {/* Chat Window */}
      {isOpen && (
        <div className="fixed bottom-24 right-6 w-96 h-96 bg-white rounded-2xl shadow-2xl shadow-teal-500/30 flex flex-col z-50 animate-in fade-in slide-in-from-bottom-4">
          {/* Header */}
          <div className="bg-gradient-to-r from-teal-500 to-teal-600 text-white p-4 rounded-t-2xl flex justify-between items-center">
            <div>
              <h3 className="font-semibold text-lg">Asistente IA</h3>
              <p className="text-xs text-teal-100">Powered by Gemini</p>
            </div>
            <button
              onClick={() => setIsOpen(false)}
              className="hover:bg-teal-700 p-1 rounded-full transition-colors"
            >
              <X size={20} />
            </button>
          </div>

          {/* Messages */}
          <div className="flex-1 overflow-y-auto p-4 space-y-3 bg-gray-50">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}
              >
                <div
                  className={`max-w-xs px-4 py-2 rounded-2xl ${
                    message.sender === 'user'
                      ? 'bg-gradient-to-r from-teal-500 to-teal-600 text-white rounded-br-none'
                      : 'bg-white text-gray-800 border border-teal-200 rounded-bl-none shadow-sm'
                  }`}
                >
                  <p className="text-sm break-words">{message.text}</p>
                </div>
              </div>
            ))}
            {isLoading && (
              <div className="flex justify-start">
                <div className="bg-white border border-teal-200 px-4 py-2 rounded-2xl rounded-bl-none shadow-sm">
                  <div className="flex gap-1">
                    <div className="w-2 h-2 bg-teal-500 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-teal-500 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-2 h-2 bg-teal-500 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                  </div>
                </div>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>

          {/* Input */}
          <form onSubmit={handleSendMessage} className="border-t border-gray-200 p-4 bg-white rounded-b-2xl">
            <div className="flex gap-2">
              <input
                type="text"
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                placeholder="Escribe tu mensaje..."
                disabled={isLoading}
                className="flex-1 bg-gray-100 border border-gray-300 rounded-full px-4 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-transparent disabled:opacity-50"
              />
              <button
                type="submit"
                disabled={isLoading || !inputValue.trim()}
                className="bg-gradient-to-r from-teal-500 to-teal-600 text-white rounded-full p-2 hover:shadow-lg hover:shadow-teal-500/50 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {isLoading ? <Loader size={18} className="animate-spin" /> : <Send size={18} />}
              </button>
            </div>
          </form>
        </div>
      )}
    </>
  );
}
